/* eslint-disable no-undef */
import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { setBarangPembelian } from "../../../actions/penjualan_action.jsx";
// import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
// import HeadTransaksiPembelian from "./HeadTransaksiPembelian.jsx";
import ModalTambahBarang from "./ModalTambahBarang.jsx";
import { reset } from "redux-form";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import ModalDataCustomer from "./ModalDataCustomer.jsx";
import { postData, postData2 } from "../../../config/axios.jsx";
import LihatDataBarang from "./LihatDataBarang.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import { hideModal, showModal } from "../../../actions/datamaster_action.jsx";
import { PageSettings } from "../../../config/page-settings.js";

const maptostate = (state) => {
  return {
    setBarangPembelian: state.penjualan.setBarangPembelian,
    kode_sales: state.form?.ModalDataCustomer?.values?.kode_sales,
    customerform: state.form?.ModalDataCustomer || false,
    jenis_pelanggan: state.form?.ModalDataCustomer?.values?.jenis_pelanggan,
    kode_customer: state.form?.ModalDataCustomer?.values?.kode_customer,
    nama_customer: state.form?.ModalDataCustomer?.values?.nama_customer,
    no_hp: state.form?.ModalDataCustomer?.values?.no_hp,
    alamat_customer: state.form?.ModalDataCustomer?.values?.alamat_customer,
  };
};
class FormTransaksiPembelian extends React.Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      step: 0,
      isLoadingBatal: false,
      isLoadingSimanBeli: false,
      isLoadingDataCustomer: false,
      isLoadingSimpanBarang: false,
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "3",
          },
          footer: "Total",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },

        {
          dataField: "kode_dept",
          text: "Jenis",
        },
        {
          dataField: "harga_nota",
          text: "Harga Nota",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },

        {
          dataField: "berat_nota",
          text: "Berat Nota",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(3)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              3
            ),
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(3)}</span>;
          },
          footer: (columnData) =>
            parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              3
            ),
        },

        {
          dataField: "harga",
          text: "Harga Beli",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "kondisi",
          text: "Kondisi",
          footer: "",
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
  }

  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = JSON.parse(
          localStorage.getItem("data_barang_pembelian_tmp")
        );
        data.splice(id, 1);
        localStorage.setItem("data_barang_pembelian_tmp", JSON.stringify(data));
        if (data.length === 0) {
          this.setState({
            step: 0,
          });
        }
        this.props.dispatch(setBarangPembelian(data));
      }
    });
  }

  simpanBarang(data) {
    // console.log(data);
    let kode = data.kondisi.split("~");
    let hasil = {
      kode_barcode:
        data.kode_barcode === undefined || "" ? "-" : data.kode_barcode,
      nama_barang: data.nama_barang,
      kondisi: kode[1],
      kadar: parseInt(data.kadar || 0),
      kode_dept: data.kode_dept,
      harga_rata: parseInt(data.harga_rata),
      harga_nota: parseInt(data.harga_nota) || 0,
      harga: parseInt(data.harga),
      berat_nota: parseFloat(data.berat_nota) || 0,
      berat: parseFloat(data.berat),
    };
    this.setState({
      hasil: hasil,
    });
    // console.log(data.kode_barcode);
    if (data.kode_barcode === undefined) {
      this.sendRequest(data);
    } else {
      if (data.harga_nota < data.harga) {
        ToastNotification("info", "Harga Melebihi Nota");
        this.props.dispatch(showModal());
        return false;
      } else {
        this.sendRequest(data);
      }
    }
  }
  sendRequest(data) {
    let kode = data.kondisi.split("~");
    this.setState({
      isLoadingSimpanBarang: true,
      step: 1,
    });
    // console.log(this.state.hasil);
    let hasil = {
      kode_barcode:
        data.kode_barcode === undefined || "" ? "-" : data.kode_barcode,
      nama_barang: data.nama_barang,
      kondisi: kode[1],
      kode_dept: data.kode_dept,
      kadar: parseInt(data.kadar || 0),
      harga_rata: parseInt(data.harga_rata),
      harga_nota: parseInt(data.harga_nota) || 0,
      harga: parseInt(data.harga),
      berat_nota: parseFloat(data.berat_nota) || 0,
      berat: parseFloat(data.berat),
    };

    if (data.kondisi === undefined) {
      return false;
    }

    let cek =
      JSON.parse(localStorage.getItem("data_barang_pembelian_tmp")) || [];
    // this.props.dispatch(hideModal());
    // document.getElementById("kode_barcode").focus();
    let hasil_cek = cek.findIndex(
      (hasil) => hasil.kode_barcode === data.kode_barcode
    );
    if (hasil_cek === -1) {
      cek.push(hasil);
      ToastNotification("success", "Barang Berhasil Ditambahkan");
      localStorage.setItem("data_barang_pembelian_tmp", JSON.stringify(cek));
      this.props.dispatch(setBarangPembelian(cek));
      this.props.dispatch(reset("ModalTambahBarang"));
      setTimeout(() => {
        this.setState({
          isLoadingSimpanBarang: false,
        });
      }, 200);
    } else {
      setTimeout(() => {
        this.setState({
          isLoadingSimpanBarang: false,
        });
      }, 200);
      document.getElementById("kode_barcode").focus();
      ToastNotification("info", "Barang Sudah Ada Di Tabel");
    }
  }

  cekOtorisasi(data) {
    let hasil = {
      user_id: data.username,
      password: data.password,
      kategori: "PEMBELIAN",
      description: "PEMBELIAN DI ATAS NOTA",
      keterangan: data.keterangan.toUpperCase() || "-",
    };
    postData2("authorization", hasil)
      .then((resposn) => {
        this.sendRequest(this.state.hasil);
        ToastNotification("success", resposn.data.message);
        localStorage.setItem("otorisasi", "false");
        this.setState({
          otorisasi: true,
        });
        this.props.dispatch(hideModal());
      })
      .catch((err) => {
        this.setState({
          otorisasi: false,
        });
        ToastNotification("info", "Otorisasi Gagal..");
      });
  }

  handleCustomer(data) {
    let hasil = {
      kode_sales: data.kode_sales,
      jenis_pelanggan: data.jenis_pelanggan,
      kode_customer: data.kode_customer,
      nama_customer: data.nama_customer,
      alamat_customer: data.alamat_customer,
      no_hp: data.no_hp,
    };
    localStorage.setItem("data_customer_pembelian_tmp", JSON.stringify(hasil));
    this.props.dispatch(hideModal());
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });

    setTimeout(() => {
      let data_barang_penjualan = JSON.parse(
        localStorage.getItem("data_barang_pembelian_tmp")
      );
      let total = 0;
      data_barang_penjualan.forEach((item, index) => {
        // console.log(item.total);
        total = total + item.harga;
      });
      let data_pembayaran = {
        jenis: "CASH",
        jumlah_rp: total,
      };

      let data_customer = JSON.parse(
        localStorage.getItem("data_customer_pembelian_tmp")
      );
      let data_pembelian = JSON.parse(
        localStorage.getItem("data_barang_pembelian_tmp")
      );

      let detail_barang = [];
      data_pembelian.forEach((item, index) => {
        let row = {
          kode_dept: item.kode_dept,
          nama_barang: item.nama_barang,
          berat_nota: item.berat_nota,
          harga: item.harga,
          berat: item.berat,
          kadar: item.kadar || 0,
          harga_nota: item.harga_nota,
          kondisi: item.kondisi,
          kode_barcode: item.kode_barcode,
        };
        detail_barang.push(row);
      });
      let datapembelian = {
        kode_sales: data_customer.kode_sales,
        nama_customer: data_customer.nama_customer,
        kode_member:
          data_customer.jenis_pelanggan === "NONMEMBER"
            ? data_customer.jenis_pelanggan
            : data_customer.kode_customer,
        alamat_customer: data_customer.alamat_customer,
        no_hp: data_customer.no_hp,
        detail_barang: detail_barang,
        pembayaran: [data_pembayaran],
      };
      // if (data.cash < data.grand_total_pembayaran) {
      //   let cash_bayar = parseInt(data.grand_total_pembayaran) - parseInt(data.cash);
      //   ToastNotification('info', 'Pembayaran Masih Kurang Rp.' + parseInt(cash_bayar).toLocaleString("kr-KO"))
      //   return false;
      // }

      postData("pembelian/simpan", datapembelian)
        .then((res) => {
          let feedback = "";
          let hasilnya = [res.data.notabeli];
          hasilnya.forEach((row, index) => {
            let jml_alamat = row.head.n_alamat_customer.length;
            feedback += `========================================\n`;
            feedback += `              NOTA PEMBELIAN            \n`;
            feedback += `========================================
Tanggal          : ${row.head.tgl_system}
Nama Customer    : ${row.head.nama_customer}
No Hp            : ${row.head.no_hp}
Alamat Customer  : ${row.head.n_alamat_customer.slice(0, 20)}\n`;
            if (jml_alamat > 20) {
              feedback += `                   ${row.head.n_alamat_customer.slice(
                20,
                40
              )}\n`;
            }
            if (jml_alamat > 40) {
              feedback += `                   ${row.head.n_alamat_customer.slice(
                40,
                60
              )}\n`;
            }
            feedback += `========================================\n`;
            feedback += `Keterangan\n`;
            feedback += `========================================\n`;
            let total = 0;
            let harga_nota = 0;
            row.detail.forEach((detail, index) => {
              total = total + detail.harga;
              harga_nota = harga_nota + detail.harga_nota;
              feedback += `No Faktur Beli : ${detail.no_faktur_beli}\n`;
              feedback += `Jenis Barang   : ${detail.kode_dept}\n`;
              feedback += `Nama Barang    : ${detail.nama_barang}\n`;
              feedback += `Berat          : ${detail.berat.toFixed(3)}\n`;
              feedback += `Harga          : ${detail.harga.toLocaleString(
                "kr-KO"
              )}\n`;
              feedback += `========================================\n`;
            });
            feedback += `Total          : ${total.toLocaleString("kr-KO")}\n`;
            feedback += `========================================\n`;
            feedback += `Harga Waktu Beli   : ${total.toLocaleString(
              "kr-KO"
            )}\n`;
            feedback += `Harga Waktu Jual   : ${harga_nota.toLocaleString(
              "kr-KO"
            )}\n`;
            feedback += `========================================\n`;
            feedback += `

`;
          });
          console.log(feedback);
          Print.postMessage(feedback);
          ToastNotification("success", res.data.message);
        })
        .then(() => {
          this.setState({
            isLoading: false,
          });
        })
        .then(() => {
          this.props.dispatch(reset("ModalPembayaran"));
        })
        .then(() => {
          localStorage.setItem(
            "data_customer_pembelian_tmp",
            JSON.stringify([])
          );
          localStorage.setItem("data_barang_pembelian_tmp", JSON.stringify([]));
        })
        .then(() => {
          this.props.dispatch(setBarangPembelian([]));
          this.setState({
            step: 0,
          });
        })
        .catch((err) => this.errorhandling(err));
    }, 500);
  }
  back(data) {
    this.setState({
      step: data,
    });
    // if (data === 1) {
    //   this.setState({
    //     step: 1,
    //   });
    // } else {
    //   this.setState({
    //     step: 0,
    //   });
    // }
  }

  simpanPembayaran(data) {
    if (data.jenis !== "CASH") {
      if (data.kembali.replace(/[^0-9]/g, "") - data.cash < 0) {
        ToastNotification("info", "Pembayaran tidak boleh lebih dari sisa");
        return false;
      }
    }
    this.setState({
      isLoading: true,
    });
    let data_barang_penjualan = JSON.parse(
      localStorage.getItem("data_barang_pembelian_tmp")
    );
    let total = 0;
    data_barang_penjualan.forEach((item, index) => {
      // console.log(item.total);
      total = total + item.harga;
    });
    // let data_pembayaran = {
    //   jenis: 'CASH',
    //   jumlah_rp: total,
    // };
    let cash = 0;
    if (data.cash > data.grand_total_pembayaran) {
      cash = data.grand_total_pembayaran;
    } else {
      cash = data.cash;
    }
    let data_pembayaran = {
      jenis: data.jenis,
      jumlah_rp: cash,
    };

    if (parseInt(data.cash) < parseInt(data.grand_total_pembayaran)) {
      let cash_bayar =
        parseInt(data.grand_total_pembayaran) - parseInt(data.cash);
      ToastNotification(
        "info",
        "Pembayaran Masih Kurang Rp." +
          parseInt(cash_bayar).toLocaleString("kr-KO")
      );
      this.setState({
        isLoading: false,
      });
      return false;
    }

    let data_customer = JSON.parse(
      localStorage.getItem("data_customer_pembelian_tmp")
    );
    let data_pembelian = JSON.parse(
      localStorage.getItem("data_barang_pembelian_tmp")
    );

    let detail_barang = [];
    data_pembelian.forEach((item, index) => {
      let row = {
        kode_dept: item.kode_dept,
        nama_barang: item.nama_barang,
        berat_nota: item.berat_nota,
        harga: item.harga,
        berat: item.berat,
        harga_nota: item.harga_nota,
        kondisi: item.kondisi,
        kode_barcode: item.kode_barcode,
      };
      detail_barang.push(row);
    });
    let datapembelian = {
      kode_sales: data_customer.kode_sales,
      nama_customer: data_customer.nama_customer,
      kode_member:
        data_customer.jenis_pelanggan === "NONMEMBER"
          ? data_customer.jenis_pelanggan
          : data_customer.kode_customer,
      alamat_customer: data_customer.alamat_customer,
      no_hp: data_customer.no_hp,
      detail_barang: detail_barang,
      pembayaran: [data_pembayaran],
    };
    // if (data.cash < data.grand_total_pembayaran) {
    //   let cash_bayar = parseInt(data.grand_total_pembayaran) - parseInt(data.cash);
    //   ToastNotification('info', 'Pembayaran Masih Kurang Rp.' + parseInt(cash_bayar).toLocaleString("kr-KO"))
    //   return false;
    // }

    postData("pembelian/simpan", datapembelian)
      .then((res) => {
        ToastNotification("success", res.data.message);
      })
      .then(() => {
        this.setState({
          isLoading: false,
        });
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        localStorage.setItem("data_customer_pembelian_tmp", JSON.stringify([]));
        localStorage.setItem("data_barang_pembelian_tmp", JSON.stringify([]));
      })
      .then(() => {
        this.props.dispatch(setBarangPembelian([]));
        this.setState({
          step: 0,
        });
      })
      .catch((err) => this.errorhandling(err));
  }

  componentWillUnmount() {
    localStorage.setItem("data_customer_pembelian_tmp", JSON.stringify([]));
    localStorage.setItem("data_barang_pembelian_tmp", JSON.stringify([]));
  }
  componentDidMount() {
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
  }
  errorhandling(error) {
    this.setState({
      isLoading: false,
    });
    ToastNotification("info", "Transaksi Gagal Disimpan");
  }
  nextLihatDataBarang() {
    // console.log();
    if (
      localStorage.getItem("data_barang_pembelian_tmp") == null ||
      JSON.parse(localStorage.getItem("data_barang_pembelian_tmp")).length === 0
    ) {
      ToastNotification("info", "Data Barang Tidak Boleh Kosong");
      return false;
    } else {
      this.setState({ step: 1 });
    }
  }
  btlPembelian() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      localStorage.setItem("data_customer_pembelian_tmp", JSON.stringify([]));
      this.props.dispatch(setBarangPembelian([]));
      localStorage.setItem("data_barang_pembelian_tmp", JSON.stringify([]));
      this.setState({
        isLoadingBatal: false,
      });
    }, 500);
  }
  render() {
    const version = process.env.REACT_APP_VERSION;
    return (
      <>
        <Link to={version === "full" ? "/SubMenuPembelian" : "/home"}>
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <div>
          <Panel>
            <PanelHeader>Transaksi Pembelian</PanelHeader>
            <div className="row mt-4">
              <div className="col-12">
                <div class="container">
                  <button
                    className="btn btn-warning mb-4"
                    type="button"
                    onClick={() => this.props.dispatch(showModal())}
                  >
                    {" "}
                    + Data Customer
                  </button>
                  <ModalTambahBarang
                    isLoadingSimpanBarang={this.state.isLoadingSimpanBarang}
                    columns={this.state.columns}
                    onSubmit={(data) => this.simpanBarang(data)}
                  />
                  <LihatDataBarang
                    isLoadingSimpanBarang={this.state.isLoadingSimpanBarang}
                    columns={this.state.columns}
                    onSubmit={(data) => this.handleSubmit(data)}
                  />
                </div>
              </div>
            </div>
            <br />
          </Panel>
          <ModalGlobal
            title="Data Customer"
            size="xl"
            content={
              <ModalDataCustomer
                isLoading={this.state.isLoading}
                back={() => this.back(1)}
                isLoadingDataCustomer={this.state.isLoadingDataCustomer}
                onSubmit={(data) => this.handleCustomer(data)}
              />
            }
          />
        </div>
      </>
    );
  }
}

export default connect(maptostate, null)(FormTransaksiPembelian);
