const ValidasiPembelian = (value) => {
  const errors = {};
  // if (!value.no_faktur) {
  //     errors.no_faktur = "No Faktur Harus Diisi";
  // }
  if (!value.kode_dept) {
    errors.kode_dept = "Kode Jenis Harus Dipilih";
  }
  if (!value.nama_barang) {
    errors.nama_barang = "Nama Barang Tidak Boleh Kosong";
  }
  // if (!value.berat_nota) {
  //     errors.berat_nota = "Berat Nota Tidak Boleh Kosong";
  // }
  // if (!value.berat) {
  //     errors.berat = "Berat Tidak Boleh Kosong";
  // }
  // if (!value.harga_nota) {
  //     errors.harga_nota = "Harga Nota Tidak Boleh Kosong";
  // }
  if (!value.harga) {
    errors.harga = "Harga Tidak Boleh Kosong";
  }
  if (!value.harga_rata) {
    errors.harga_rata = "Harga Rata Tidak Boleh Kosong";
  }
  if (!value.kondisi) {
    errors.kondisi = "Kondisi Harus Dipilih";
  }

  return errors;
};

export default ValidasiPembelian;
