
import React, { Component } from "react";
import { connect } from "react-redux";
import { formValueSelector, reduxForm } from "redux-form";
import { getDataJenis, getDataSales, getKondisiPembelian } from "../../../actions/datamaster_action";
import Tabel from "../../../components/helpers/tabel";
import ValidasiPembelian from "../../../Validasi/Pembelian/ValidasiPembelian";

class LihatDataBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      harga_jual: true,
      hargapergram: true,
      ongkos: true,
      total: true,
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getKondisiPembelian());
  }

  render() {
      return (
        
          <div className="row">
          <div className="col-12">
            <form 
            onSubmit={this.props.handleSubmit}
            onKeyPress={(e) => {
              e.key === "Enter" && e.preventDefault();
            }}
            autoComplete="off">
              <div>
              <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={
                this.props.setBarangPembelian.length === 0
                  ? JSON.parse(
                      localStorage.getItem("data_barang_pembelian_tmp")
                    )
                  : this.props.setBarangPembelian
              }
              // tambahData={true}
              // btnText="Tambah Data Customer"
              columns={this.props.columns}
              // btnOptional={true}
              // btnTextOptional="Tambah Data Barang"
              // handleClick={() => this.ShowModalCustomer()}
              // handleClickOptional={() => this.ShowModalBarang()}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
           
          </div>
          <div className="col-lg-12 text-right">
          <button
              className="btn btn-primary btn-block btn-lg"
              disabled={this.props.isLoadingSimpanBarang}
              type="submit"
            >
              {this.props.isLoadingSimpanBarang ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Selesai"
              )}
            </button>
              </div>
            </form>
              </div>
     </div>
    );
  }
}

LihatDataBarang = reduxForm({
    form: "LihatDataBarang",
    enableReinitialize: true,
    validate:ValidasiPembelian
})(LihatDataBarang);
// export default connect(maptostate, null)(LihatDataBarang);

const selector = formValueSelector("LihatDataBarang"); // <-- same as form name

export default connect((state) => {
  const { harga, berat } = selector(
    state,
    "harga",
    "berat"
  );
  return {
    setBarangPembelian: state.penjualan.setBarangPembelian,
    harga_rata: parseFloat(harga || 0) / parseFloat(berat || 0) || 0,
    DataSales: state.datamaster.getDataSales,
    datajenis: state.datamaster.getDataJenis,
    datakondisi: state.datamaster.getKondisiPembelian,
  };
})(LihatDataBarang);
